import React, { Component } from 'react';
import { Route, Redirect, Switch } from "react-router-dom";
import Group from "./group";
import GroupMaterial from "./group/material";
import history from './group/history';

class GroupRouter extends Component {
  render() {
    return (
    	<div>
        <Route path='/admin/gonghao/tuoguan/'>
          <Switch>
            <Route path="/admin/message/group/index" component={Group} />
            <Route path="/admin/message/group/material/:code" component={GroupMaterial} />
            <Route path="/admin/message/group/history/:code" component={history} />
            <Redirect from="/admin/message" exact to='/admin/message/group' />
            <Redirect from="/admin/message/group" exact to='/admin/message/group/index' />
          </Switch>
        </Route>
    	</div>
    )
  }
}

export default GroupRouter;